.mission {
  background: var(--white);
  &__items {
    --mission-gap: 94px;
    display: grid;
    grid-gap: calc(var(--mission-gap) * 2);
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 30px;
  }

  &__item {
    padding-top: 70px;
    background-position: top center;
    background-repeat: no-repeat;

    &:not(:first-child){
      &::after{
        content: '' ;
        display: inline-block;
        width: 1px;
        height: 100%;
        position: absolute;
        left: 0px;
        top: 0px;
        left: calc(-1 * (var(--mission-gap)));
        background: url(../img/divider.svg) no-repeat center / cover;
      }
    }


    &--quality {
      background-image: url(../img/like.svg);
    }

    &--safety {
      background-image: url(../img/hand.svg);
    }

    &--comfort {
      background-image: url(../img/slippers.svg);
    }
  }

  &__title {
    margin-bottom: 13px;
  }
}

.know-form {
  box-shadow: 0 4px 4px -4px rgba(30, 33, 44, 0.03), 0 12px 10px -6px rgba(154, 156, 165, 0.04), 0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 80px 80px -20px rgba(154, 156, 165, 0.08);
  background: var(--white);
  padding: 43px 60px;
  position: relative;
  top: 105px;

  &__title{
    margin-bottom: 37px;
  }

  &__content {
    @include flex-all-end;
    gap: 24px;
  }

  &__field {
    min-width: 260px;
  }
}




